.app {
  display: flex;
  gap: 5px;
}
.container {
  width: 70%;
  margin-right: auto;
  margin-top: 150px;
  border-radius: 20px 0 0 20px;
  /* height: 100px; */
  background-color: #fff;
  padding: 4px 30px 30px;
  max-height: calc(100vh - 250px);
  min-height: 225px;
  overflow-y: auto;
}

.container > h2 {
  text-decoration: underline;
  font-size: 32px;
  color: #008;
}
.container .users .user {
  display: flex;
  font-weight: bold;
  justify-content: space-between;
  font-size: 23px;
  color: #222;
  border-bottom: 1px solid black;
  align-items: center;
}

.container .users .user:not(:first-of-type) > div {
  border-right: 1px solid black;
}
.container .users .user > div {
  padding: 5px 10px;
}
.container .users .user > .id {
  width: 10%;
}
.container .users .user > div:not(:first-of-type) {
  width: 30%;
}
.container .users .user:not(:first-of-type) > div button {
  color: white;
  background-color: #080;
  outline: none;
  border: none;
  /* width: 16%; */
  font-size: 17px;
  cursor: pointer;
  border-top: 1px solid white;
  padding: 5px 0;
}
.container .users .user:not(:first-of-type) > div button:last-of-type {
  background-color: #800;
}

.add-form {
  width: 30%;
  min-width: 260px;
  margin-left: auto;
  margin-top: 150px;
  border-radius: 0 20px 20px 0;
  /* height: 100px; */
  background-color: #fff;
  padding: 6px 12px;
}

.add-form > h2 {
  margin-left: 10px;
  text-decoration: underline;
  color: #008;
}

.add-form form > div,
.update form > div {
  display: flex;
  gap: 5px;
  margin: 5px 0;
}

.add-form form > div input:not([type="submit"]),
.update form > div input:not([type="submit"]) {
  width: 100%;
  height: 34px;
  border: none;
  background-color: #eee;
  outline: none;
  padding-left: 5px;
  border-radius: 4px;
}

.add-form form > div input[type="submit"],
.update form > div input[type="submit"] {
  width: 50%;
  height: 34px;
  border: 1px solid black;
  background-color: #008;
  outline: none;
  padding-left: 5px;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

@media (max-width: 825px) {
  .app {
    flex-direction: column-reverse;
    align-items: center;
    text-align: center;
  }
  .add-form {
    margin: auto;
    width: 90%;
    padding: 30px;
    border-radius: 20px;
    padding-top: 13px;
  }
  .container {
    width: 90%;
    border-radius: 20px;
    margin: auto;
    margin-top: 10px;
    overflow-y: visible;
    max-height: none;
  }
}

.update {
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, hidden 0.3s;
}
.update .update-container {
  background-color: white;
  border-radius: 30px;
  padding: 40px;
}

.update .update-container form > div {
  margin: 10px;
}
.update .update-container form > div input {
  height: 40px;
}

.update .update-container form > div input:not([type="submit"]) {
  padding-left: 10px;
}

.user input {
  height: 31px;
  outline: none;
  border: 1px solid black;
  font-size: 15px;
  background: #eee;
  margin: 0 10px;
  padding-left: 10px;
  width: 32%;
}
